.app {
  max-width: 1400px;
  margin: auto; }
  .app .search {
    display: block;
    width: 99%;
    margin: 0 auto; }
  .app .video-player {
    position: relative;
    padding-bottom: 56.23%;
    /* Use 75% for 4:3 videos */
    height: 0;
    overflow: hidden;
    max-width: 100%;
    background: #000;
    margin: 5px 15px; }
    .app .video-player iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
      background: transparent; }
    .app .video-player img {
      bottom: 0;
      display: block;
      left: 0;
      margin: auto;
      max-width: 100%;
      width: 100%;
      position: absolute;
      right: 0;
      top: 0;
      border: none;
      height: auto;
      cursor: pointer;
      -webkit-transition: .4s all;
      transition: .4s all; }
      .app .video-player img:hover {
        -webkit-filter: brightness(75%); }
    .app .video-player video {
      position: absolute;
      right: 0;
      top: 0;
      max-height: 100%;
      width: 100%; }
    .app .video-player .play {
      height: 72px;
      width: 72px;
      left: 50%;
      top: 50%;
      margin-left: -36px;
      margin-top: -36px;
      position: absolute;
      background: url("//i.imgur.com/TxzC70f.png") no-repeat;
      cursor: pointer; }
  .app .ellipsis {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden; }
  .app .column3 {
    width: 30%;
    float: left;
    padding: 0 10px; }
    .app .column3 p {
      font-size: 14px;
      margin: 10px 15px; }

.modalContent {
  text-align: -moz-center;
  text-align: -webkit-center;
  height: 100%; }
  .modalContent img {
    height: inherit; }

.ReactModal__Overlay {
  z-index: 99999999; }

body {
  margin: 0;
  background: lightgray;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

